import Dmsconfirm from "@/components/Dmsconfirm.vue";
import { getAppealListbyid, getAppealinfobyid } from "@/api/appeal";
import { onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import Stuformappeal from "@/components/Stuformappeal.vue";
export default {
  name: "stuappeal",
  components: {
    Dmsconfirm,
    Stuformappeal
  },
  setup() {
    let appeallist = reactive({
      list: [{
        id: 1,
        time: "2023-02-17 23:26",
        type: "建议",
        appealstu: "方文轩",
        stunumber: "3119001333",
        phone: "13356580997",
        building: "48",
        room: "202"
      }]
    });
    let appealinfo = reactive({
      info: [{
        type: "类型",
        content: "正文",
        time: "2023-03-01 17:16",
        appealstu: "学生一"
      }]
    });
    const store = useStore();
    const settable = () => {
      setappealtable(1);
      setappealinfo(0);
    };
    const setappealtable = val => {
      getAppealListbyid({
        page: val,
        stuid: store.state.dsuser.dsuserInfo.id
      }).then(res => {
        if (res.status == 0) {
          appeallist.list = res.appeallist;
          appealtotal.value = Object.values(res.appealcount[0])[0];
          changebg();
        } else {
          console.log(res.message);
        }
      }).catch(err => {
        console.log(err.message);
      });
    };
    const setappealinfo = val => {
      getAppealinfobyid({
        id: val,
        stuid: store.state.dsuser.dsuserInfo.id
      }).then(res => {
        if (res.status == 0) {
          if (res.appealinfo.length == 0) {
            appealinfo.info = [{
              type: "类型",
              content: "正文",
              time: "2023-03-01 17:16",
              appealstu: "学生一"
            }];
          } else {
            appealinfo.info = res.appealinfo;
            store.commit("dsappeal/setActiveAppealpage", currentAppealPage.value);
          }
        } else {
          console.log(res.message);
        }
      }).catch(err => {
        console.log(err.message);
      });
    };
    const deleteid = ref(0);
    const deletestr = ref("确定要删除该诉求吗");
    const deletetype = ref(13);
    const handleAppealDelete = (index, row) => {
      // console.log(index, row);
      deleteid.value = row.id;
      deletestr.value = "确定要删除该诉求吗";
      deletetype.value = 13;
    };
    const handleAppealEdit = (index, row) => {
      // console.log(index, row);
      store.commit("dsappeal/setAppealFormId", row.id);
      store.commit("dsappeal/setAppealFormType", 1);
      store.commit("dsappeal/setAppealFormStatus");
    };
    const currentAppealPage = ref(1);
    const appealtotal = ref(100);
    const appealPageDisabled = ref(false);
    const handleAppealCurrentChange = val => {
      setappealtable(val);
    };
    const changebg = () => {
      setTimeout(() => {
        let index = 0;
        document.querySelectorAll(".appealtable .el-table__row").forEach(item => {
          if (!item.getAttribute("index")) {
            item.addEventListener("click", function () {
              store.commit("dsappeal/setActiveAppeal", item.getAttribute("index"));
              document.querySelectorAll(".appealtable .el-table__row").forEach(item1 => {
                item1.classList.remove("activeappealcolor");
              });
              item.classList.add("activeappealcolor");
            });
          }
          item.setAttribute("index", index);
          index++;
        });
        if (store.state.dsappeal.activeappealpage == currentAppealPage.value) {
          document.querySelectorAll(".appealtable .el-table__row").forEach(item => {
            if (item.getAttribute("index") == store.state.dsappeal.activeappeal) {
              item.classList.add("activeappealcolor");
            }
          });
        } else {
          document.querySelectorAll(".appealtable .el-table__row").forEach(item => {
            item.classList.remove("activeappealcolor");
          });
        }
      }, 500);
    };
    const rowclick = function (row) {
      // console.log(row);
      setappealinfo(row.id);
    };
    const sendappeal = () => {
      // console.log("发送诉求");
      store.commit("dsappeal/setAppealFormType", 0);
      store.commit("dsappeal/setAppealFormStatus");
    };
    onMounted(() => {
      setappealtable(1);
      setappealinfo(0);
    });
    return {
      appeallist,
      handleAppealDelete,
      handleAppealEdit,
      currentAppealPage,
      appealtotal,
      appealPageDisabled,
      handleAppealCurrentChange,
      rowclick,
      appealinfo,
      sendappeal,
      deleteid,
      setappealtable,
      deletetype,
      deletestr,
      settable
    };
  }
};